@charset "UTF-8";

// Initialize
// Import
@import "_calc.scss";
// 変数の定義(Global)
$global-font-size: 14px;
$gradation-pattern: linear-gradient(to right, #000011, #1164FF, #FFFF99);
$width-list: (xs: 576, sm: 768, md: 992, lg: 1200, lx: 1400, hd: 1920) !default;

@mixin min-screen($breakpoint: md) {
	@media only screen and (min-width: #{map-get($width-list, $breakpoint) + 1}px) {
		@content; } }

@mixin max-screen($breakpoint: md) {
	@media only screen and (max-width: #{map-get($width-list, $breakpoint)}px) {
		@content; } }

/// Viewport Widthの演算
@function calc-viewport($value: 1000, $breakpoint: md) {
	@return round-decimal(($value / map-get($width-list, $breakpoint)) * 100, 3) + vw; }


/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * 共通項目
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */

* {
	margin: 0;
	padding: 0;
	line-height: 1.2; }

html {
	// rem指定用
	font-size: $global-font-size; }

body {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	font-family: "メイリオ", "Meiryo", sans-serif;
	font-size: 1rem;
	color: #FFFFFF;
	background-color: #000011;

	@include max-screen(md) {
		display: flex;
		flex-direction: column;
		justify-content: unset;
		align-items: unset; }

	@media print {
		color: #000000;
		background-color: unset;

	 > header {}
		@media print {
			display: none; }

	 > input {}
		&[type="checkbox"] {
			display: none;

			&.panel-switch {
				&:not(:checked) {
					+ nav {
						@include max-screen(md) {
							// display: none
							overflow: hidden;
							height: 0;
							padding: 0;
							border-width: 0; } } } } }

	 > .content {}
		display: block;
		width: calc(100% - 240px);
		margin-left: 240px;

		@include max-screen(md) {
			width: 100%;
			margin-left: 0; }

		@media print {
			width: 100%;
			margin-left: 0; } } }

a {
	transition: all 0.25s ease-out;
	text-decoration: none;
	color: #9999FF;

	&:hover {
		color: #FFCC66; } }

h1, h2, h3, h4, h5, h6 {
	font-family: "游ゴシック体", "Yu Gothic", "YuGothic", "ヒラギノ角ゴシック Pro", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", "Osaka", "ＭＳ Ｐゴシック", "MS PGothic", sans-serif; }

dl {
	dd {
		margin-left: 2rem; } }

ul {
	list-style: none;

	li {
		&:not(:first-child) {
			margin-top: 5px; } }

	ul {
		margin-left: 2rem; } }

ol {
	margin-left: 2rem;

	li {
		&:not(:first-child) {
			margin-top: 5px; } } }

// クラス設定
.md {
	&-hide {
		@include max-screen(md) {
			display: none; } }

	&-show {
		@include min-screen(md) {
			display: none; } } }

.mintyo {
	font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif; }

.stub, .cation, .deprecated {
	margin: 10px;
	padding: 20px;
	border-width: 5px;
	border-style: solid;

	@media print {
		color: #FFFFFF; } }

.stub {
	border-image: linear-gradient(to top right, #1164FF, #000011, #1164FF) 1/5px 5px/0 stretch;
	background-color: #000033; }

.cation {
	border-image: linear-gradient(to top right, #FFFF00, #000011, #FFFF00) 1/5px 5px/0 stretch;
	background-color: #333300; }

.deprecated {
	border-image: linear-gradient(to top right, #1164FF, #FF0F0F, #1164FF) 1/5px 5px/0 stretch;
	background-color: #330000; }

// ベンダー固有情報
::-webkit-scrollbar {
	width: 10px;
	height: 10px;

	&-track {
		// border-radius: 10px
		background-color: transparent;
 }		// box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1)

	&-thumb {
		transition: all 0.1s ease;
		border-radius: 10px;
		background-image: linear-gradient(to bottom right, rgba(255, 255, 255, 0.75), rgba(255, 255, 255, 0.25));
		box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.3); } }

// アニメーション設定
@keyframes flashString {
	0% {
		color: #FFFFFF;
		text-shadow: 0 0 0 rgba(255, 255, 255, 0); }

	90% {
		color: #FFFFFF;
		text-shadow: 0 0 0 rgba(255, 255, 255, 0); }

	95% {
		color: #FFFF66;
		text-shadow: 0 0 10px #FFFF66; }

	100% {
		color: #FFFFFF;
		text-shadow: 0 0 0 rgba(255, 255, 255, 0); } }


/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * Prism Overwrite
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */

code, pre {
	&[class*="language-"] {
		margin: 10px 0;
		tab-size: 4;
		font-family: "Migu 1M", Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
		line-height: 1.2;

		@include max-screen(xs) {
			max-height: 80vh; }

		-webkit-overflow-scrolling: touch;
		@media print {
 }			// color: #000000

		&::-webkit-scrollbar {
			width: 5px;
			height: 5px; } } }

.line-highlight {
	@media print {
		display: none; } }


/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * サイドバー
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */

// @keyframes openList
