.sidebar {
	position: fixed;
	top: 0;
	left: 0;
	overflow-y: scroll;
	box-sizing: border-box;
	width: 240px;
	min-width: 240px;
	height: 100vh;
	min-height: 100vh;
	padding: 10px 0 2rem;
	border-width: 0;
	border-style: solid;
	border-right-width: 3px;
	border-image: linear-gradient(to bottom, #000011, #1164FF) 1/0 3px 0 0/0 stretch;

	@include max-screen(md) {
		position: static;
		width: 100%;
		height: auto;
		min-height: unset;
		margin-bottom: 20px;
		padding-left: 2px;
		transition: all 0.5s ease-out;
		border-bottom-width: 3px;
		border-left-width: 3px;
		border-image: linear-gradient(to bottom, #000011, #1164FF) 1/0 3px 5px/0 stretch; }

	@media print {
		display: none;

	 > ul {}
		$menu-margin: 10px;

		@include max-screen(md) {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			padding: 0 5px;
			padding-bottom: 10px;

		 > li {}
			margin-top: 15px;

			@include max-screen(md) {
				margin-top: 10px; }

			@include max-screen(sm) {
				margin-top: 5px; }

			h3 {
				display: flex;
				align-items: center;
				padding-top: 3px;
				padding-left: $menu-margin;
				animation: flashString 10s linear infinite;
				font-size: 1.25rem;
				border-width: 0;
				border-style: solid;
				border-top-width: 3px;
				border-image: linear-gradient(to right, #000011, #1164FF, #FFFF66) 1/3px 0 0/0 stretch;

				@include max-screen(md) {
					padding-left: 0; }

				a {
					display: inline-flex;
					align-items: center;
					animation: flashString 10s linear infinite; } }

			input {
				&[type="checkbox"] {
					display: none;

					&.panel-switch {
						&:not(:checked) {
							+ ul {
								// display: none
								overflow: hidden;
								height: 0;
								padding: 0;
								border-width: 0; } } } } }

			ul {
				margin-left: calc(1em + #{$menu-margin + 2px});
				padding-right: 2px;
				transition: all 0.5s ease-out;
				// animation: openList 0.2s linear all
				text-indent: -1em;

				@include max-screen(md) {
					margin-left: calc(1em + 2px); }

				li {
					transition: all 0.5s ease-out;
					&:before {
						padding-right: 2px;
						content: "├";
						transition: all 0.5s ease-out;
						animation: flashString 10s linear infinite; }

					&:last-child {
						&:before {
							padding-right: 2px;
							content: "└";
							transition: all 0.5s ease-out;
							animation: flashString 10s linear infinite; } } } } } }

	&::-webkit-scrollbar {
		width: 5px;
		height: 5px;

		@include max-screen(md) {
			width: 0;
			height: 0; }

		&-track {
			border-radius: 10px;
			background-image: linear-gradient(to bottom, #000011, #1164FF); }

		&-thumb {
			border-radius: 10px;
			background-image: linear-gradient(to bottom right, #CCCCCC, #888888);
 } } }			// box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.3)

.nav-button {
	display: flex;
	justify-content: space-evenly;

	@supports (-ms-ime-align:auto) or ((-webkit-text-size-adjust:none) and (-webkit-marquee-repetition:infinite) and (object-fit:fill)) {
		justify-content: space-around; }

	button {
		width: 100px;
		height: 24px;
		font-size: 0.9rem;
		color: #FFFFFF;
		border: none;
		border-radius: 5px;
		background-color: #1164FF; } }

.expand-box {
	display: flex;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
	width: 20px;
	height: 20px;
	margin: auto 5px auto 0;
	cursor: pointer;
	font-size: 0.9rem;
	border: 1px #FFFFFF solid; }


/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * ヘッダー
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */

header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 1rem;
	margin-bottom: 3px;
	border-width: 0;
	border-style: solid;
	border-bottom-width: 5px;
	border-image: $gradation-pattern 1/0 0 5px/0 stretch;

	@media print {
		margin-top: 0; }

	h1 {
		padding-left: 2.5rem;
		font-size: 2.5rem;

		@include max-screen(sm) {
			font-size: 4.7vw; } }

	label {
		display: none;

		@include max-screen(md) {
			display: inline-flex;
			justify-content: center;
			align-items: center;
			width: 160px;
			height: 32px;
			margin-right: 5px;
			margin-bottom: 5px;
			border-radius: 5px;
			background-color: #1164FF; } } }


/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * コンテンツ
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */

section {
	margin-top: 10px;

	&:first-child {
		margin-top: 5px; }

	&:not(:first-child) {
		padding-top: 5px;
		border-width: 0;
		border-style: solid;
		border-top-width: 2px;
		border-image: $gradation-pattern 1/2px 0 0/0 stretch; }

	h1, h2, h3, h4, h5, h6, p {
		padding: 0 0.5rem; }

	h2 {
		font-size: 1.75rem; }

	h3 {
		margin-top: 5px;
		margin-left: 5px;
		padding-left: 5px;
		font-size: 1.25rem;
		border-left: 10px solid #1164FF;

 }		// -webkit-box-reflect: below -10px -webkit-linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 10%, rgba(0, 0, 0, 0.6))

	h4 {
		margin-top: 5px;
		font-size: 1rem; }

	ul {
		margin-left: 2rem;
		list-style: disc;

		li {
			padding-right: 10px; } }

	dl {
		padding-left: 0.5rem;

		dt {
			h1, h2, h3, h4, h5, h6 {
				padding: 0; }

			h3 {
				padding-left: 5px; } }

		dd {
			p {
				padding: 0; }

			ul {
				li {
					&:first-child {
						margin-top: 5px; }

					&:last-child {
						margin-bottom: 5px; } } } } } }

.object {
	&-safe {
		color: #33FF33; }

	&-euclid {
		color: #FFFF33; }

	&-keter {
		color: #FF3333; } }


/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * フッター
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */

footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 3px;
	padding-top: 5px;
	padding-bottom: 5px;
	border-width: 0;
	border-style: solid;
	border-top-width: 5px;
	border-bottom-width: 20px;
	border-image: $gradation-pattern 1/5px 0 20px/0 stretch;

	@include max-screen(sm) {
		flex-direction: column-reverse; }

	p {
		@include max-screen(sm) {
			box-sizing: border-box;
			width: 100%; }

		&:first-child {
			a {
				padding: 0 0 0 0.5rem;
				animation: flashString 10s linear infinite;
				text-decoration: line-through;
				color: unset;

				@media print {
					animation: unset;
					color: #000000; } }

			b {
				font-weight: 900; } }

		&:last-child {
			margin-left: 2rem;
			text-align: right;
			white-space: nowrap;

			@include max-screen(sm) {
				margin-bottom: 15px;
				margin-left: 0; }

			@include max-screen(xs) {
				white-space: normal; } } } }


/* --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 * レスポンシブの設定
 * --------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
 */
